import '../styles/index.scss';

let isMobile = false;
if (/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
    isMobile = true;
}
//fullpage js
(function (window, document) {
    var sectionsCount = document.querySelectorAll('.section').length;

    var sectionsAnchors = [];
    for (var i = 1; i <= sectionsCount; i++) {
        sectionsAnchors.push('' + i);
    }
    const menuItems = document.querySelectorAll('#menu .menu__item');
    const sliderMenu = document.querySelector('#slider');
    const sliderMenuButtonLeft = sliderMenu.querySelector('.slider._left');
    const sliderMenuButtonRight = sliderMenu.querySelector('.slider._right');

    $('#fullpage').fullpage({
        scrollingSpeed: 0,
        paddingTop: '75px',
        responsiveWidth: 780,
        anchors: sectionsAnchors,
        afterLoad: function afterLoad(origin, destination, direction) {
            //menu slider
            console.dir(origin)
            if (origin > 1 && origin < 13) {
                sliderMenuButtonLeft.href = `#${origin - 1}`;
                sliderMenuButtonRight.href = `#${origin + 1}`;
            } else if (origin == 13) {
                sliderMenuButtonLeft.href = `#${origin - 1}`;
            } else if (origin == 1) {
                sliderMenuButtonRight.href = `#${origin + 1}`;
            }
            //menu switch
            menuItems.forEach(menuItem => {
                const dataMenuItem = +menuItem.dataset.menu;

                if (this[0].dataset.section == dataMenuItem) {
                    actionMenu(menuItem, menuItems);
                }
            });
        }
    });
    //methods
    $.fn.fullpage.setAllowScrolling(false);
})(window, document);

function showNextSection(section) {
    console.dir('asd')
        let nextSection = section.nextElementSibling;

        nextSection.classList.add('_active');
}
function actionMenu(elem, menuItemsArray) {
    menuItemsArray.forEach(menuItem => {
        menuItem.classList.remove('_active');
    });
    elem.classList.add('_active');
}
//modal window
var modal = new tingle.modal({
    footer: false,
    stickyFooter: false,
    closeMethods: ['escape', 'button'],
    closeLabel: "Закрыть",
    cssClass: ['custom-class-1', 'custom-class-2'],
    beforeOpen: function () {
        const closeWindowButton = modal.modalBox.querySelector('.button._close');
        if (closeWindowButton) {
            if (isMobile) {
                let currentSection = document.querySelector('.section._active');
                showNextSection(currentSection)
                currentSection.classList.remove('_active');
            }
            closeWindowButton.addEventListener('click', e => {
                console.dir('asd3')
                modal.close();
            });
        }

        //modal slider
        const sliderButtons = modal.modalBox.querySelectorAll('.button.slider');
        if (sliderButtons) {
            sliderButtons.forEach(sliderButton => {
                sliderButton.addEventListener('click', e => {
                    e.preventDefault();
                    console.dir(sliderButton)
                    modal.close();
                    setContentModalWindow(sliderButton)
                });
            });
        }  
    }
});
const modalsButtons = document.querySelectorAll('[data-modal]');
modalsButtons.forEach(modalButton => {
    modalButton.addEventListener('click', e => {
        setContentModalWindow(modalButton);
    });
});

function setContentModalWindow(modalButton) {
    let modalTmplId = modalButton.dataset.modal;
    let modalTmpl = document.querySelector(`#${modalTmplId}`);
    let cloneModalTmpl = modalTmpl.cloneNode(true);
    let modalContent = cloneModalTmpl.content;

    ga('bronhiplet.send', 'event', 'button', 'modal', modalTmplId)
    modal.setContent(modalContent);
    modal.open();
}
if (isMobile) {
    const allDocuemntButtons = document.querySelectorAll('.button');

    allDocuemntButtons.forEach(test => {
        let newLink = test.hash.split('#')[1];
        
        if (newLink) {
            test.hash = `s${newLink}`;
            test.addEventListener('click', e => {
                console.dir('asd2')
                let currentSection = document.querySelector('.section._active');
                showNextSection(currentSection)
                currentSection.classList.remove('_active');
            });
        }

        
    })
}
